import './style.css';
import React, { Component } from 'react';

class Aliment extends Component {

  constructor(props) {
    super(props);
    this.state = {
      aliment : this.props.aliment,
    };
  }

  render() {
    //this.state.aliment ? this.renderExisting() : this.renderEmpty();
    return (
      <span>
        {this.props.aliment.label} : {this.props.energyPer100g} kcal/100g, kcal/unit {this.props.energyPerUnit}
      </span>
    );
  }
}

export default Aliment;



