import './App.css';
import React, { Component } from 'react';
import AlimentEditor from './components/aliment-editor/AlimentEditor';
import AlimentModel from './models/AlimentModel';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      aliment: new AlimentModel('Poire'),
    }
  }

  // updateAliment(event) {
  //   const name = event.target.name;
  //   this.setState({
  //     aliment: {
  //       [name]: event.target.value
  //     }
  //   });
  // }

  render() {
    return (
      <div className="container-fluid">
        <h1>App Title</h1>
        <AlimentEditor aliment={this.state.aliment} />
      </div>
    );
  }
}

export default App;



