import './style.css';
import React, { Component } from 'react';
import Aliment from '../aliment/Aliment';
import AlimentModel from '../../models/AlimentModel';

class AlimentEditor extends Component {

  constructor(props) {
    super(props);
    this.state = {
      aliment: props.aliment ? props.aliment : new AlimentModel("Poulet"),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleSubmit(event) {
    localStorage.setItem('aliement', JSON.stringify(this.state.aliment));
  }

  handleChange(event) {
    const name = event.target.name;
    this.setState({
      aliment: {
        [name]: event.target.value
      }
    });
  }

  storeData(data) {
    document.cookie = "session test : ca marche";
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <Aliment aliment={this.state.aliment} />

        <div className="form-group">
          <label htmlFor="AliementEditorLabel">Label</label>
          <input type="text"
            className="form-control"
            id="AliementEditorLabel"
            placeholder="His name is ..."
            name="label"
            value={this.state.aliment.label}
            onChange={this.handleChange} />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPer100g">Energy per 100g</label>
          <input type="text"
            className="form-control"
            id="AliementEditorEnergyPer100g"
            placeholder="Calories per 100g"
            name="energyPer100g"
            value={this.state.aliment.energyPer100g}
            onChange={this.handleChange} />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPerUnit">Energy per unit</label>
          <input type="text"
            className="form-control"
            id="AliementEditorEnergyPerUnit"
            placeholder="Calories per unit"
            name="energyPerUnit"
            value={this.state.aliment.energyPerUnit}
            onChange={this.handleChange} />
        </div>

        {/* <div className="form-group">
          <label htmlFor="exampleFormControlSelect2">Composition</label>
          <select multiple className="form-control" id="exampleFormControlSelect2">
            <option>1</option>
            <option>2</option>
            <option>3</option>
            <option>4</option>
            <option>5</option>
          </select>
        </div>
        <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1">Description</label>
          <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
        </div> */}
        <button type="submit" className="btn btn-primary">Submit</button>
      </form>
    );
  }
}

export default AlimentEditor;



